'use strict';

export default function($stateProvider) {
  'ngInject';
  var coupletoforceImages = [];
  var imgPath1_1a="/assets/images/ch4_fig1/ch4_fig1000";
  var imgPath2_1a="/assets/images/ch4_fig1/ch4_fig100";
  var imgPath3_1a="/assets/images/ch4_fig1/ch4_fig10";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      coupletoforceImages.push(imgPath1_1a + i + '.png');
    }
    else if (i < 100){
      coupletoforceImages.push(imgPath2_1a + i + '.png');
    }
    else{
      coupletoforceImages.push(imgPath3_1a + i + '.png');
    }
  }
  var imgPath1_2a="/assets/images/ch4_fig3/ch4_fig3000";
  var imgPath2_2a="/assets/images/ch4_fig3/ch4_fig300";
  var imgPath3_2a="/assets/images/ch4_fig3/ch4_fig30";
  for(var i = 0; i < 117; i++){
    if (i <= 9){
      coupletoforceImages.push(imgPath1_2a + i + '.png');
    }
    else if (i < 100){
      coupletoforceImages.push(imgPath2_2a + i + '.png');
    }
    else{
      coupletoforceImages.push(imgPath3_2a + i + '.png');
    }
  }
  var imgPath1_3a="/assets/images/ch4_fig6/ch4_fig8000";
  var imgPath2_3a="/assets/images/ch4_fig6/ch4_fig800";
  var imgPath3_3a="/assets/images/ch4_fig6/ch4_fig80";
  for(var i = 0; i < 168; i++){
    if (i <= 9){
      coupletoforceImages.push(imgPath1_3a + i + '.png');
    }
    else if (i < 100){
      coupletoforceImages.push(imgPath2_3a + i + '.png');
    }
    else{
      coupletoforceImages.push(imgPath3_3a + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter04-couple-to-force-ratio', {
      url: '/chapter04-couple-to-force-ratio',
      template: '<chapter-04-couple-to-force-ratio></chapter-04-couple-to-force-ratio>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(coupletoforceImages);
        }
      }
    });
}



