'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter05-review-questions.routes';

export class Chapter05ReviewQuestionsComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter05-review-questions', [uiRouter])
  .config(routes)
  .component('chapter05ReviewQuestions', {
    template: require('./chapter05-review-questions.html'),
    controller: Chapter05ReviewQuestionsComponent,
    controllerAs: 'chapter05ReviewQuestionsCtrl'
  })
  .name;



