'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './third-law.routes';

export class ThirdLawComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.third-law', [uiRouter])
  .config(routes)
  .component('thirdLaw', {
    template: require('./third-law.html'),
    controller: ThirdLawComponent,
    controllerAs: 'thirdLawCtrl'
  })
  .name;



