'use strict';

export default function($stateProvider) {
  'ngInject';
  var effectofbiology = [];
  var imgPath1_1a="/assets/images/ch4_fig35A/ch4_fig35A000";
  var imgPath2_1a="/assets/images/ch4_fig35A/ch4_fig35A00";
  for(var i = 0; i < 35; i++){
    if (i <= 9){
      effectofbiology.push(imgPath1_1a + i + '.png');
    }
    else{
      effectofbiology.push(imgPath2_1a + i + '.png');
    }
  }
  var imgPath1_1b="/assets/images/ch4_fig35B/ch4_fig35B000";
  var imgPath2_1b="/assets/images/ch4_fig35B/ch4_fig35B00";
  for(var i = 0; i < 40; i++){
    if (i <= 9){
      effectofbiology.push(imgPath1_1b + i + '.png');
    }
    else{
      effectofbiology.push(imgPath2_1b + i + '.png');
    }
  }
  var imgPath1_1c="/assets/images/ch4_fig36A/ch4_fig36A000";
  var imgPath2_1c="/assets/images/ch4_fig36A/ch4_fig36A00";
  for(var i = 0; i < 40; i++){
    if (i <= 9){
      effectofbiology.push(imgPath1_1c + i + '.png');
    }
    else{
      effectofbiology.push(imgPath2_1c + i + '.png');
    }
  }
  var imgPath1_1d="/assets/images/ch4_fig36B/ch4_fig36B000";
  var imgPath2_1d="/assets/images/ch4_fig36B/ch4_fig36B00";
  for(var i = 0; i < 40; i++){
    if (i <= 9){
      effectofbiology.push(imgPath1_1d + i + '.png');
    }
    else{
      effectofbiology.push(imgPath2_1d + i + '.png');
    }
  }

  $stateProvider
    .state('home.chapter04-effect-of-biology', {
      url: '/chapter04-effect-of-biology',
      template: '<chapter-04-effect-of-biology></chapter-04-effect-of-biology>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(effectofbiology);
        }
      }
    });
}



