'use strict';

export default function($stateProvider) {
  'ngInject';
  var thirdlawImages = [];
  var imgPath1_1="/assets/images/ch5_fig8/ch5_fig8000";
  var imgPath2_1="/assets/images/ch5_fig8/ch5_fig800";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      thirdlawImages.push(imgPath1_1 + i + '.png');
    }else{
      thirdlawImages.push(imgPath2_1 + i + '.png');
    }
  }
  $stateProvider
    .state('home.third-law', {
      url: '/third-law',
      template: '<third-law></third-law>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(thirdlawImages);
        }
      }
    });
}



