const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter06-recommended-reading.routes';

export class Chapter06RecommendedReadingComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter06-recommended-reading', [uiRouter])
  .config(routes)
  .component('chapter06RecommendedReading', {
    template: require('./chapter06-recommended-reading.html'),
    controller: Chapter06RecommendedReadingComponent,
    controllerAs: 'chapter06RecommendedReadingCtrl'
  })
  .name;



