'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './equilibrium-intro.routes';

export class EquilibriumIntroComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.equilibrium-intro', [uiRouter])
  .config(routes)
  .component('equilibriumIntro', {
    template: require('./equilibrium-intro.html'),
    controller: EquilibriumIntroComponent,
    controllerAs: 'equilibriumIntroCtrl'
  })
  .name;



