const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './two-couple-system-advanced.routes';

export class TwoCoupleSystemAdvancedComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.two-couple-system-advanced', [uiRouter])
  .config(routes)
  .component('twoCoupleSystemAdvanced', {
    template: require('./two-couple-system-advanced.html'),
    controller: TwoCoupleSystemAdvancedComponent,
    controllerAs: 'twoCoupleSystemAdvancedCtrl'
  })
  .name;



