	module.exports = "<div>\r\n  <h1 class=\"tit_2\"><a id=\"009\"></a><a id=\"66\"></a>Chapter 8</h1>\r\n<h1 class=\"tit\">Two-Couple System: Advanced</h1>\r\n<p class=\"indent\">This chapter first describes conditions under which <span class=\"Italic\">v-bends or step-bends</span> are induced in flexible wires, and then focuses on step-bends and the forces and moments that are produced by this system directly or indirectly in rigid and flexible wires. The role of different variables such as bracket size, wire shape, position of bend, and interbracket distance are analyzed. We demonstrate that <span class=\"Italic\">combining v-bend and step-bend</span> can change the magnitude and direction of forces and moments. In addition, we introduce some advanced topics in two-couple systems such as the effect of the <span class=\"Italic\">vertical position</span> of the brackets, <span class=\"Italic\">horizontal forces</span>, and <span class=\"Italic\">freedom and restriction of movement</span>, which can be applied for both v-bends and step-bends.</p>\r\n<div>\r\n  <a ui-sref=\"home.bends-in-flexible-wires\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n</div>"

