	module.exports = "<div>\r\n  <h1 class=\"tit\"><a id=\"67\"></a>V-bend or Step-bend in Flexible Wires</h1>\r\n<p class=\"indent\">Previously, we described how the position of brackets determines the shape of the bend (v-bend or a step-bend) in flexible wires extending between two brackets. What conditions dictate each type of bend?</p>\r\n<p class=\"indent\">Position of brackets can be defined based on the occlusal plane. Each bracket forms an angle with the occlusal plane (&#945;<sub>1</sub> and &#945;<sub>2</sub>). If these angles are in opposite directions, and the lines that represent the plane of each bracket slot cross each other at a point between the two brackets (red zone), the wire will adapt a v-bend configuration (<a href=\"#fig-ch8-1\">Figure 8.1A,B</a>). If both brackets are in the same vertical position in relation to the occlusal plane, the bracket with the higher angle will be closer to the v-bend (<a href=\"#fig-ch8-1\">Figure 8.1B</a>). This is true only if brackets are at the same height. Changing the vertical position of the bracket without changing the angulation will also affect the position of the v-bend, which will in turn affect the force and moments that appear in the system. The properties of these different configurations when the brackets are at the same height have been discussed in Chapter 7. The effect of changes in the vertical height of brackets on the forces and moments is discussed later in this chapter.</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch8_fig1.png\" alt=\"images\" /></p>\r\n<p class=\"caption1\" id=\"fig-ch8-1\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 8.1 Configuration of a flexible wire engaged into two brackets with opposite angulation to the occlusal plane. When the angles that brackets make with occlusal plane are in opposite direction of each other (</span>&#945;<span class=\"Italic\"><sub>1</sub> opposite</span> &#945;<span class=\"Italic\"><sub>2</sub>), the wire will adopt a v-bend shape. If you extend a line along the brackets&#8217; slot (dotted line), they will cross each other in the shape of v-bend at a point between the two brackets (red zone) (A). Maintaining the vertical position of the brackets while changing the angulation of brackets will move the v-bend from a midpoint between two brackets to a point closer to one bracket (B). Changing the vertical position of a bracket without changing the angulation will also change the position of the v-bend and therefore the resultant forces and moments (C).</span></p>\r\n<p class=\"indent\">As you can see from <a href=\"#fig-ch8-1\">Figure 8.1</a>, as long as the lines representing each bracket cross each other at a point between the brackets (red zone), they will induce a v-shaped bend in the wire.</p>\r\n<p class=\"indent\">In the above example, both brackets were angulated in opposite direction in relation to the occlusal plane. What happens if one of the brackets is parallel to the occlusal plane (&#945;<sub>1</sub>=0) or angulated in the same direction as the other bracket (the acute angles that both brackets make with occlusal plane are in the same direction; &#945;<sub>1</sub>= &#945;<sub>2</sub> in <a href=\"#fig-ch8-2\">Figure 8.2</a>)?</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch8_fig2.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch8-2\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 8.2 Different angulation between two brackets located at similar height from occlusal plane. In one scenario, the slot in one bracket is parallel to occlusal plane and the other makes an acute angle with occlusal plane (A). In an alternative scenario both bracket slots make acute angles with occlusal plane but in the same direction (B).</span></p>\r\n<p class=\"indent\">In these scenarios, the dotted lines that represent each bracket slot will not cross between the brackets (red zone). Instead, they will cross at a point on one of the brackets (<a href=\"#fig-ch8-3\">Figure 8.3A</a>), or at a point past both of the brackets (<a href=\"#fig-ch8-3\">Figure 8.3B</a>). They also may never cross (<a href=\"#fig-ch8-3\">Figure 8.3C</a>). In all the above examples, the dotted lines are crossing each other at a point away from the area between the two brackets (green zone).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch8_fig3.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch8-3\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 8.3 Relation between two brackets located at the same height but with different angulation to the occlusal plane. When one bracket is parallel to occlusal plane and another bracket forms an angle with occlusal plane, the dotted line that represents each slot will cross each other on one bracket (green zone) (A). Increasing the angulation of one bracket moves the crossing point behind the bracket (green zone) (B). Further increasing the angulation of both brackets will push the crossing point deeper in green zone (C) or to infinity.</span></p>\r\n<p class=\"indent\">In the above examples, a flexible wire that extends between the two brackets will adopt either a combination of a v-bend and step-bend shape, or a pure step-bend shape (<a href=\"#fig-ch8-4\">Figure 8.4</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch8_fig4.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch8-4\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 8.4 A flexible wire will adopt a combination of v-bend and step-bend when extending between two brackets of similar heights and different angulations with the occlusal plane. When one bracket is parallel and the other forms an angle to the occlusal plane, a flexible wire will adopt a combination of v-bend and step-bend (purple dotted line in A represents v-bend, and blue dotted line represents step-bend). The magnitude of the v-bend will decrease as the angulation of bracket parallel to the occlusal plane increases (B). When the angulation of both brackets is completely similar, a pure step-bend will appear in the wire (blue dotted line, C).</span></p>\r\n<p class=\"indent\">Similar to v-bends, the above argument is correct only when both brackets are at the same height relative to the occlusal plane, and only the angulation of brackets is changing. If one bracket is parallel to the occlusal plane, and we keep the angulation of the other bracket and just change its vertical position, we will see that the cross-point between lines extending from the brackets&#8217; slots will move from the green zone into red zone and therefore the configuration is no longer a step-bend (<a href=\"#fig-ch8-5\">Figure 8.5</a>) but rather a v-bend shape.</p>\r\n<p class=\"img\"><a id=\"play1\" onclick=\"play1()\"><img id=\"img-1\" style=\"max-width:100%;\" src=\"/assets/images/ch8_fig5/ch8_fig50000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig-ch8-5\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 8.5 Changing the vertical position of one bracket changes the configuration of the wire. If the vertical position of one bracket changes without altering the angulation, the intersection of the dotted lines extending from the bracket slot moves from the green zone into the red zone. A flexible wire inserted into the brackets will change its configuration from a step-bend into a v-bend shape.</span></p>\r\n<p class=\"indent\">Based on these observations, it is clear that predicting the forces and moments produced in a flexible wire as a result of bracket angulation, by solely analyzing the angles of brackets, will have limited usage. Thus, we recommend that both the vertical position and angulation of brackets be considered in this prediction. In this regard, determining the crossing point between the two lines that represent the bracket slot is a more accurate determinant.</p>\r\n<div>\r\n   <a ui-sref=\"home.two-couple-system-advanced\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.step-bend-forces-moments\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n<script type=\"text/javascript\">\r\n    var startImage1=0;\r\n    var endImage1=59;\r\n      var counter1=0;\r\n      var imgPath1_1=\"/assets/images/ch8_fig5/ch8_fig5000\";\r\n      var imgPath2_1=\"/assets/images/ch8_fig5/ch8_fig500\";\r\n    var interval1;\r\n      function play1() {\r\n         var img=document.getElementById(\"img-1\");\r\n             document.getElementById(\"play1\").onclick=\"\";\r\n         interval1= setInterval(function(){\r\n           if(counter1<10)\r\n          img.src=imgPath1_1+counter1+\".png\";\r\n           else\r\n            img.src=imgPath2_1+counter1+\".png\";\r\n          counter1++;\r\n          if(counter1>endImage1) {\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1\").onclick=play1;\r\n            //img.src=imgPath1_1+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop1(){\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1\").onclick=play1;\r\n            img.src=imgPath2_1+endImage1+\".png\";\r\n}\r\n</script>\r\n</div>\r\n"

