	module.exports = "<div>\r\n<script>\r\n    var startImage1=0;\r\n    var endImage1=34;\r\n      var counter1=0;\r\n      var imgPath1_1=\"/assets/images/ch7_fig15/ch7_fig15A000\";\r\n      var imgPath2_1=\"/assets/images/ch7_fig15/ch7_fig15A00\";\r\n    var interval1;\r\n      function play1() {\r\n         var img=document.getElementById(\"img-1\");\r\n             document.getElementById(\"play1\").onclick=\"\";\r\n         interval1= setInterval(function(){\r\n           if(counter1<10)\r\n          img.src=imgPath1_1+counter1+\".png\";\r\n           else\r\n            img.src=imgPath2_1+counter1+\".png\";\r\n          counter1++;\r\n          if(counter1>endImage1) {\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1\").onclick=play1;\r\n            //img.src=imgPath1_1+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop1(){\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1\").onclick=play1;\r\n            img.src=imgPath2_1+endImage1+\".png\";\r\n}\r\n    var startImage2=0;\r\n    var endImage2=29;\r\n      var counter2=0;\r\n      var imgPath1_2=\"/assets/images/ch7_fig16/ch7_fig16000\";\r\n      var imgPath2_2=\"/assets/images/ch7_fig16/ch7_fig1600\";\r\n    var interval2;\r\n      function play2() {\r\n         var img=document.getElementById(\"img-2\");\r\n             document.getElementById(\"play2\").onclick=\"\";\r\n         interval2= setInterval(function(){\r\n           if(counter2<10)\r\n          img.src=imgPath1_2+counter2+\".png\";\r\n           else\r\n            img.src=imgPath2_2+counter2+\".png\";\r\n          counter2++;\r\n          if(counter2>endImage2) {\r\n            clearInterval(interval2);\r\n            counter2=0;\r\n                        document.getElementById(\"play2\").onclick=play2;\r\n            //img.src=imgPath1_2+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop2(){\r\n            clearInterval(interval2);\r\n            counter2=0;\r\n                        document.getElementById(\"play2\").onclick=play2;\r\n            img.src=imgPath2_2+endImage2+\".png\";\r\n}\r\n    var startImage3=0;\r\n    var endImage3=39;\r\n      var counter3=0;\r\n      var imgPath1_3=\"/assets/images/ch7_fig19/ch7_fig19000\";\r\n      var imgPath2_3=\"/assets/images/ch7_fig19/ch7_fig1900\";\r\n    var interval3;\r\n      function play3() {\r\n         var img=document.getElementById(\"img-3\");\r\n             document.getElementById(\"play3\").onclick=\"\";\r\n         interval3= setInterval(function(){\r\n           if(counter3<10)\r\n          img.src=imgPath1_3+counter3+\".png\";\r\n           else\r\n            img.src=imgPath2_3+counter3+\".png\";\r\n          counter3++;\r\n          if(counter3>endImage3) {\r\n            clearInterval(interval3);\r\n            counter3=0;\r\n                        document.getElementById(\"play3\").onclick=play3;\r\n            //img.src=imgPath1_3+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop3(){\r\n            clearInterval(interval3);\r\n            counter3=0;\r\n                        document.getElementById(\"play3\").onclick=play3;\r\n            img.src=imgPath2_3+endImage3+\".png\";\r\n}\r\n    var startImage4=0;\r\n    var endImage4=37;\r\n      var counter4=0;\r\n      var imgPath1_4=\"/assets/images/ch7_fig20/ch7_fig20000\";\r\n      var imgPath2_4=\"/assets/images/ch7_fig20/ch7_fig2000\";\r\n    var interval4;\r\n      function play4() {\r\n         var img=document.getElementById(\"img-4\");\r\n             document.getElementById(\"play4\").onclick=\"\";\r\n         interval4= setInterval(function(){\r\n           if(counter4<10)\r\n          img.src=imgPath1_4+counter4+\".png\";\r\n           else\r\n            img.src=imgPath2_4+counter4+\".png\";\r\n          counter4++;\r\n          if(counter4>endImage4) {\r\n            clearInterval(interval4);\r\n            counter4=0;\r\n                        document.getElementById(\"play4\").onclick=play4;\r\n            //img.src=imgPath1_4+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop4(){\r\n            clearInterval(interval4);\r\n            counter4=0;\r\n                        document.getElementById(\"play4\").onclick=play4;\r\n            img.src=imgPath2_4+endImage4+\".png\";\r\n}\r\n    var startImage5=0;\r\n    var endImage5=62;\r\n      var counter5=0;\r\n      var imgPath1_5=\"/assets/images/ch7_fig22/ch7_fig22000\";\r\n      var imgPath2_5=\"/assets/images/ch7_fig22/ch7_fig2200\";\r\n    var interval5;\r\n      function play5() {\r\n         var img=document.getElementById(\"img-5\");\r\n             document.getElementById(\"play5\").onclick=\"\";\r\n         interval5= setInterval(function(){\r\n           if(counter5<10)\r\n          img.src=imgPath1_5+counter5+\".png\";\r\n           else\r\n            img.src=imgPath2_5+counter5+\".png\";\r\n          counter5++;\r\n          if(counter5>endImage5) {\r\n            clearInterval(interval5);\r\n            counter5=0;\r\n                        document.getElementById(\"play5\").onclick=play5;\r\n            //img.src=imgPath1_5+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop5(){\r\n            clearInterval(interval5);\r\n            counter5=0;\r\n                        document.getElementById(\"play5\").onclick=play5;\r\n            img.src=imgPath2_5+endImage5+\".png\";\r\n}\r\n</script>\r\n<h1 class=\"tit\"><a id=\"54\"></a>Asymmetrical V-bend</h1>\r\n<p class=\"indent\">Now let&#8217;s see what happens when, in a similar situation, we place an asymmetrical v-bend (<a href=\"#fig-ch7-14\">Figure 7.14</a>). Is this going to change the resultant forces and moments on each tooth?</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig14.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-14\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.14 Introducing an asymmetrical v-bend to a rigid wire can activate the wire and cause each tooth to receive different forces and moments.</span></p>\r\n<p class=\"indent\">Before we start to analyze our system, let&#8217;s look at the effect of asymmetrical v-bend on angle of activation using the same analogy as above. Assume you are standing between two brackets, but you are closer to one of the brackets. You lift the two wires that extend into each of the brackets to the same height with your right and left hands. The angle of activation will be larger in which side? As discussed earlier, the closer the bracket is to the v-bend, the larger the angle of activation, which means the larger the couple (<a href=\"#fig-ch7-15\">Figure 7.15</a>).</p>\r\n<p class=\"img\"><a id=\"play1\" onclick=\"play1()\"><img id=\"img-1\" style=\"max-width:100%;\" src=\"/assets/images/ch7_fig15/ch7_fig15A0000.png\"/></a></p>\r\n<p class=\"caption1\" id=\"fig-ch7-15\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.15 Different angles of activation result in different couples and moments. If you stand closer to one bracket and you lift the two wires to the same height, the shorter wire will have a larger angle of activation (</span>&#945;<span class=\"Italic\"><sub>1</sub> is larger than</span> &#945;<span class=\"Italic\"><sub>2</sub>) and therefore that bracket will receive a larger moment than the bracket farther away from your hand. The moments will also have opposite directions.</span></p>\r\n<p class=\"indent\">Similarly, introducing an asymmetrical v-bend in a rigid wire extending into two identical brackets will generate different moments in the two-couple system&#8212;with the tooth closer to the v-bend receiving the larger moment (<a href=\"#fig-ch7-16\">Figure 7.16</a>).</p>\r\n<p class=\"img\"><a id=\"play2\" onclick=\"play2()\"><img id=\"img-2\" style=\"max-width:100%;\" src=\"/assets/images/ch7_fig16/ch7_fig160000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig-ch7-16\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.16 Effect of an asymmetrical v-bend in a two-couple system. Introducing an asymmetrical v-bend in this setup will generate moments with different magnitudes and opposite directions due to the difference in the angle of activation between the two brackets.</span></p>\r\n<p class=\"indent-tno\">For comprehensive analysis of our system, let&#8217;s divide our two-couple system into two one-couple systems again. We are going to show only the final forces and moments that the teeth will receive with the assumption that you are familiar with the analysis at the wire level.</p>\r\n<p class=\"indent-tno\">If we have one bracket on the premolar and one button on the canine, in response to a v-bend that is closer to the premolar, a larger moment will appear on the premolar and, at the same time, both the premolar and canine will receive forces that are equal in magnitude but opposite in direction (<a href=\"#fig-ch7-17\">Figure 7.17</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig17.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-17\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.17 Resultant forces and moment in a one-couple system with a bracket on the premolar and a button on the canine. An asymmetrical v-bend will generate extrusion and intrusion forces (straight red arrows) in this system in addition to a counterclockwise moment on the premolar (curved red arrow).</span></p>\r\n<p class=\"indent\">What happens if we keep the asymmetrical v-bend as it is and we add a bracket to the canine and a button on the premolar? In response to the asymmetrical v-bend, vertical forces and moment will appear in our system, but this time the magnitude of forces and moments are smaller because the position of v-bend is farther away from the canine bracket (<a href=\"#fig-ch7-18\">Figure 7.18</a>). If you recall, in Chapter 6, in the one-couple system, the farther the v-bend is from the bracket, the smaller the magnitude of forces and moments.</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig18.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-18\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.18 Resultant forces and moment in a one couple system with a bracket on the canine and a button on the premolar. In response to an asymmetrical v-bend, extrusion and intrusion forces are generated (straight green arrows) in this system in addition to a clockwise moment on the canine (curved green arrow).</span></p>\r\n<p class=\"indent\">If we add the result of these two one-couple systems together, you will notice that the premolar will receive an intrusion force while canine will receive an extrusion force. In addition, two opposite moments will appear in the system with the tooth closer to the v-bend receiving the larger moment&#8212;in our example, the premolar (<a href=\"#fig-ch7-19\">Figure 7.19</a>).</p>\r\n<p class=\"img\"><a id=\"play3\" onclick=\"play3()\"><img id=\"img-3\" style=\"max-width:100%;\" src=\"/assets/images/ch7_fig19/ch7_fig190000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig-ch7-19\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.19 Effect of an asymmetrical v-bend in a two-couple system. The sum of forces and moments that appear in the system result in a extrusion force in the canine and an intrusion force in the premolar, and a moment on each tooth with opposite directions. The premolar, being closer to the v-bend, receives the larger magnitude moment.</span></p>\r\n<p class=\"indent\">If you look carefully at the final result in <a href=\"#fig-ch7-19\">Figure 7.19</a>, you will notice that a specific relation exists between forces and moments. If the larger moment is represented by M<sub>1</sub> and the smaller moment by M<sub>2</sub> , the vertical forces present in the system can generate an imaginary couple (which in reality is not a couple since forces are applied on two teeth , not one), resulting in a moment in the opposite direction of larger moment. The magnitude of this moment (M<sub>3</sub>) is equivalent to the sum of M<sub>1</sub> and M<sub>2</sub> moments (M<sub>1</sub> &#43; M<sub>2</sub> = M<sub>3</sub>) (<a href=\"#fig-ch7-20\">Figure 7.20</a>).</p>\r\n<p class=\"img\"><a id=\"play4\" onclick=\"play4()\"><img id=\"img-4\" style=\"max-width:100%;\" src=\"/assets/images/ch7_fig20/ch7_fig200000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig-ch7-20\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.20 Overall moments in an asymmetrical v-bend where M<sub>3</sub> = M<sub>2</sub> &#43; M<sub>1</sub>.</span></p>\r\n<p class=\"indent\">At chairside we would like to be able to analyze quickly all the forces and moments that are applied to the teeth by an asymmetrical v-bend in a two-couple system. To quickly predict the forces and moments applied to each tooth, follow these steps: First, recognize the direction of the moments. If you consider a v-bend as an angle, the larger moment that appears in response to asymmetrical v-bends in rigid wires is always toward inside the angle (<a href=\"#fig-ch7-21\">Figure 7.21A</a>) while the direction of the small moment based on position of the v-bend can be different (effect of position of v-bend on moments and forces is discuss later in this chapter). Second, you need to recognize which tooth receives the larger moment (M<sub>1</sub>). The tooth that is closer to the v-bend always receives the larger moment (<a href=\"#fig-ch7-21\">Figure 7.21B</a>). Third, you need to see the direction of the forces that appear in the system. The forces make an imaginary couple resulting in a moment (M<sub>3</sub>) in the opposite direction of the large moment (<a href=\"#fig-ch7-21\">Figure 7.21C</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig21.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-21\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.21 Simple stepwise analysis of resultant forces and moments generated in response to an asymmetrical v-bend. First, always draw the direction of moments toward the inside of the angle of v-bend (A). Second, remember that the bracket closer to the v-bend receives the larger moment (M<sub>1</sub>) (B). Finally, visualize an imaginary couple in your mind that represents the forces that each tooth will receive (C). This couple generates a moment (M<sub>3</sub>) in opposite direction of the larger moment (M<sub>2</sub>) and its magnitude equals M<sub>1</sub>&#43; M<sub>2</sub>.</span></p>\r\n<p class=\"indent\">What happens if we are placing a flexible wire between two adjacent brackets that have opposite angulation and the magnitude of their angulation is not the same (<a href=\"#fig-ch7-22\">Figure 7.22</a>)? In this condition, flexible wire will adopt an asymmetrical v-bend configuration, where v-bend will be closer to the bracket with the higher angulation. In this case, the angle that the wire makes with the occlusal plane (the angle of activation) will be different too, with the larger angle of activation occurring in the bracket closer to the v-bend.</p>\r\n<p class=\"img\"><a id=\"play5\" onclick=\"play5()\"><img id=\"img-5\" style=\"max-width:100%;\" src=\"/assets/images/ch7_fig22/ch7_fig220000.png\"/></a></p>\r\n<p class=\"caption1\" id=\"fig-ch7-22\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.22 Angulation of brackets can induce an asymmetrical v-bend in a flexible wire. Brackets on adjacent teeth with opposite angulations of different magnitude can produce an asymmetrical v-bend that will be closer to the bracket with the larger angulation. This results in a larger angle of activation on that bracket (</span>&#945;<span class=\"Italic\"><sub>2</sub>) than the one on the other side of the v-bend (</span>&#945;<span class=\"Italic\"><sub>1</sub>).</span></p>\r\n<p class=\"indent\">If we draw the reaction forces and moments that the wire applies to the teeth, we notice that the tooth with higher angulation will receive the largest moment, the tooth with the lowest angulation will receive the smaller moment, and both teeth will receive opposite but equal forces (<a href=\"#fig-ch7-23\">Figure 7.23</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig23.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-23\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.23 Forces and moments that appear in a two-couple system with an asymmetrical v-bend created by a flexible wire. The bracket with the highest angulation will receive the larger moment, while the bracket with the smaller angulation will receive the smaller moment and both teeth will receive opposite but equal forces.</span></p>\r\n<p class=\"indent\">As discussed, the v-bend that we place in the rigid wire and the v-bend that is induced in the flexible wire by angulation of the brackets will have opposite directions of resultant forces and moments, if the direction of v-bend is similar (<a href=\"#fig-ch7-24\">Figure 7.24</a>). Whereas the direction of the larger moment is toward the inside of the angle of v-bend in rigid wires, in the bracket-induced v-bend, the direction of larger moment is toward the outside of the angle of v-bend. In both cases the large moment will be closer to the bracket that is closer to the v-bend. The magnitude of forces and moments in both systems will depend on the property of the wire and location of the v-bend.</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch7_fig24.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch7-24\">&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 7.24 Forces and moments generated by similar asymmetrical v-bends in rigid versus flexible wires. The asymmetrical v-bend created by bending a rigid wire with a plier (A) generates forces (straight red arrows) and moments (curved purple arrow, M<sub>1</sub> and M<sub>2</sub>) with opposite directions to the ones produced by a v-bend induced in flexible wire by the angulation brackets (B). Note that the v-bend has the same direction in (A) and (B).</span></p>\r\n  \r\n<div>\r\n   <a ui-sref=\"home.chapter-07-symmetrical-v-bend\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.chapter-07-controlling-factors\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n</div>\r\n"

