	module.exports = "<div>\r\n<h2 class=\"level2\"><a id=\"43\"></a><span>Other Moments in Our System</span></h2>\r\n<p class=\"indent\"><span>It should be emphasized that forces and moment produced during the application of a one-couple system are characteristics of the appliance (attachment and wire) and are produced independent from the center of resistance of the teeth. In other words, if we do not change any components of a one-couple system setup (attachment, wire, or angle of engagement of wire inside bracket), it will produce the same magnitude of forces and moment regardless of the size or angulation of teeth or surrounding alveolar bone. However, since forces produced by one-couple systems may or may not pass through the center of resistance of an individual tooth, they may produce additional moments. These are not part of the one-couple system equation and the equilibrium of the wire, but can still affect individual tooth movement significantly (<a href=\"#fig-ch6-7\">Figure 6.7</a>). The size, angulation of the tooth, and surrounding alveolar bone can affect the magnitude of these secondary moments that appear in our system. In addition, the moment on one tooth does not affect the other tooth.</span></p>\r\n<p class=\"img\"><a id=\"play4\" onclick=\"play4()\"><img id=\"img-4\" style=\"max-width:100%;\" src=\"/assets/images/ch6_fig7/ch6_fig7_new0000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig-ch6-7\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span class=\"Italic\">Figure 6.7 The vertical forces produced by the one-couple system can generate additional moments in different dimensions. In the sagittal view, the forces pass through the center of resistance of the teeth, whereas in the frontal view, they do not pass through center of resistance and therefore they can produce side effects such as buccal rotation of molar or palatal rotation of the canine.</span></p>\r\n<div>\r\n   <a ui-sref=\"home.chapter06-force-and-moments-in-one-couple-system\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.chapter06-controlling-factors-bracket-size\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n<script>\r\n    var startImage4=0;\r\n    var endImage4=52;\r\n      var counter4=0;\r\n      var imgPath1_4=\"/assets/images/ch6_fig7/ch6_fig7_new000\";\r\n      var imgPath2_4=\"/assets/images/ch6_fig7/ch6_fig7_new00\";\r\n    var interval4;\r\n      function play4() {\r\n         var img=document.getElementById(\"img-4\");\r\n             document.getElementById(\"play4\").onclick=\"\";\r\n         interval4= setInterval(function(){\r\n           if(counter4<10)\r\n          img.src=imgPath1_4+counter4+\".png\";\r\n           else\r\n            img.src=imgPath2_4+counter4+\".png\";\r\n          counter4++;\r\n          if(counter4>endImage4) {\r\n            clearInterval(interval4);\r\n            counter4=0;\r\n                        document.getElementById(\"play4\").onclick=play4;\r\n            //img.src=imgPath1_4+startImage+\".png\";\r\n          }\r\n         },100);\r\n      }\r\nfunction stop4(){\r\n            clearInterval(interval4);\r\n            counter4=0;\r\n                        document.getElementById(\"play4\").onclick=play4;\r\n            img.src=imgPath2_4+endImage4+\".png\";\r\n}\r\n</script>\r\n</div>\r\n"

