	module.exports = "<div><h1 class=\"tit\"><a id=\"46\"></a><span>Considerations of One-Couple System</span></h1>\r\n<p class=\"indent\">There are a number of factors that can significantly impact the forces and moment produced by a one-couple system. These factors should be carefully evaluated when designing your setup depending on the movements that you wish to achieve.</p>\r\n<h2 class=\"level2\"><a id=\"44\"></a><span>Effect of Bracket Size</span></h2>\r\n<p class=\"indent\"><span>As discussed, a one-couple system is produced due to the rotation of the wire in the attachment (bracket or tube) which in turn produces a couple. Also, the magnitude of the couple produced by the attachment affects the magnitude of forces that appear in the system. Therefore, the size of the attachment has a significant effect on the one-couple system. Considering nothing is changed in our system (premolar and canine stay in the same position and wire does not change), if we use narrower bracket on the premolar, the magnitude of the couple that is produced in the bracket will be less and therefore the magnitude of forces that appear in our system will be less (<a href=\"#fig-ch6-8\">Figure 6.8</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch6_fig8.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch6-8\"><span class=\"Italic\">Figure 6.8 Effect of bracket width on the magnitude of forces and moments that are produced by the one-couple system. Increasing the size of the bracket (A) produces a larger force and moment on the teeth than a narrower bracket (B).</span></p>\r\n<p class=\"indent\"><span>If the attachment becomes so small that you cannot produce a couple at all (such as tying the wire to a button), your forces can decrease to zero. This can be seen clinically since the wire will not show any deflection and will extend in a straight manner between the two buttons (<a href=\"#fig-ch6-9\">Figure 6.9</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch6_fig9.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig-ch6-9\"><span class=\"Italic\">Figure 6.9 When the wire is tied from both sides to buttons, the magnitude of the forces and the couple drops to zero. This system is no longer a one-couple system and it will not produce any forces or moments.</span></p>\r\n<p class=\"indent\">Please pay attention that if you are using a narrow bracket, and you want to produce similar moment as with a wide bracket either you need to use a stiffer wire (as discussed later), or you need to activate the wire significantly to produce larger couples at the corner of the bracket to compensate for the narrow width. Since the height of the attachment on the adjacent tooth will dictate the magnitude of activation of the straight wire, a bend should be introduced to the wire to bypass this limitation (see role of v-bend in a one-couple system). If the magnitude of the activation is big enough , the moment that is produced by the wider bracket is equal to the magnitude of the moment that is produced by narrower bracket, and so will the magnitude of forces that both one-couple systems are producing on the teeth (<a href=\"#fig-ch6-10\">Figure 6.10</a>).</p>\r\n<div class=\"main_fig_10\">\r\n<div class=\"main_fig_10_A\">\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch6_fig10A.png\" alt=\"images\" /></p>\r\n</div>\r\n<div class=\"main_fig_10_B\">\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch6_fig10B.png\" alt=\"images\" /></p>\r\n</div>\r\n</div>\r\n<p class=\"caption\" id=\"fig-ch6-10\"><span class=\"Italic\">Figure 6.10 Different brackets can produce similar forces and moments but the magnitude of the couple is different. Both one-couple systems (A and B) produce the same force (F</span><sub>1</sub><span class=\"Italic\">), but for a narrow bracket (B) to produce similar moment as a wider bracket (A), the couple increases significantly (F</span><sub>3</sub> <span class=\"Italic\">&#62; F</span><sub>2</sub><span class=\"Italic\">).</span></p>\r\n<div>\r\n   <a ui-sref=\"home.chapter06-other-moments\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.chapter06-material-and-size-of-wire\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n</div>\r\n"

