	module.exports = "<div>\r\n  <h1 class=\"tit_3\"><a id=\"49\"></a><span>Summary</span></h1>\r\n<p class=\"indent\"><span>In this chapter we learned about the one-couple system generated when a section of a wire is engaged on one side in a bracket or tube to produce a couple (two contact points) and tied on the other side to an attachment (one contact point). We discuss the effect of the bracket design, wire characteristics, and interaction between them on the resultant forces and moments that are produced directly or indirectly by this system. In the next chapter we review what happens when both sides of the wire are engaged inside the bracket slots (producing a couple in both sides). We also discuss the properties of this design in different dimensions, and how adding a v-bend or step bend to the wire can increase the complexity of the design by changing not only the magnitude of the forces and moments, but also their direction.</span></p>\r\n<div>\r\n   <a ui-sref=\"home.chapter06-review-questions\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.chapter06-recommended-reading\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n</div>\r\n"

