	module.exports = "<div>\r\n  \r\n  <h1 class=\"tit_2\"><a id=\"005\"></a><a id=\"28\"></a>Chapter 4</h1>\r\n<h1 class=\"tit\">Couple to Force Ratio</h1>\r\n<p class=\"indent\">This chapter discusses which <i>couple to force ratio</i> (M<sub>C</sub>/F) is necessary to produce different <i>types of tooth movement</i> while maintaining the point of application of the force at the level of the bracket. In addition, the important role of this concept is demonstrated in different clinical scenarios, such as retraction or protraction of a tooth in different dimensions. This information is helpful during the selection of material, shape and size of the wire, and the amount of activation needed to produce the desired type of tooth movement.</p>\r\n\r\n<div>\r\n  <a ui-sref=\"home.chapter04-couple-to-force-ratio\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n</div>\r\n"

