	module.exports = "<div>\r\n  <h1 class=\"tit\"><a id=\"31\"></a>Different M<sub>C</sub>/F Using Round or Rectangular Wires</h1>\r\n<p class=\"indent\">The use of round wires in the first and second order views can produce all types of tooth movement because it is possible to produce couples in these dimensions. Using a round wire in the third order view can only produce uncontrolled tipping. It is possible to apply additional force to the round wires to produce a moment in the opposite direction to counteract the moment produced by the force, but this additional force may or may not be beneficial (<a href=\"#fig4.16\">Figure 4.16</a>).</p>\r\n<p class=\"img\"><a id=\"play1a\" onclick=\"play1a()\"><img id=\"img1a\" style=\"max-width:100%;\" src=\"/assets/images/ch4_fig16/ch4_fig160000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig4.16\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<i>Figure 4.16. Application of two forces simultaneously by a round wire to produce different M<sub>C</sub>/F in third order view</i>.</p>\r\n<p class=\"indent\">Another factor that influences the final movement when using round wires is the inclination of the tooth. In many situations, to produce an opposite moment, we need to apply a significant amount of force (<a href=\"#fig4.17\">Figure 4.17</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch4_fig17.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig4.17\"><i>Figure 4.17. To produce translation using a round wire, sometimes you need significant amounts of secondary force that can be harmful. In this example, F2 has to be threefold larger than F1</i>.</p>\r\n<p class=\"indent\">With rectangular wires we are able to produce couples in all three order views. However, the wire must be large enough to contact the bracket in two points on opposite sides in order to produce a couple. Rectangular wires with smaller dimensions act in a similar manner as round wires. However, larger rectangular wires may not produce the required couple if they are not activated enough (<a href=\"#fig4.18\">Figure 4.18</a>).</p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/ch4_fig18.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig4.18\"><i>Figure 4.18. Rectangular wires do not produce couples when they are undersized or inactive</i>.</p>\r\n<div>\r\n   <a ui-sref=\"home.chapter04-effect-of-magnitude-of-force\" class=\"btn btn-default pull-left\"><i class=\"fa fa-reply\" aria-hidden=\"true\"></i>\r\n    </a>\r\n</div>\r\n<div>\r\n  <a ui-sref=\"home.chapter04-during-retraction\" class=\"btn btn-default pull-right\"><i class=\"fa fa-share\" aria-hidden=\"true\"></i></a>\r\n</div>\r\n<script type=\"text/javascript\">\r\n    var startImage1a=0;\r\n    var endImage1a=74;\r\n    var counter1=0;\r\n    var imgPath1_1a=\"/assets/images/ch4_fig16/ch4_fig16000\";\r\n    var imgPath2_1a=\"/assets/images/ch4_fig16/ch4_fig1600\";\r\n    var interval1;\r\n    function play1a() {\r\n         var img=document.getElementById(\"img1a\");\r\n             document.getElementById(\"play1a\").onclick=\"\";\r\n         interval1= setInterval(function(){\r\n           if(counter1<10)\r\n          img.src=imgPath1_1a+counter1+\".png\";\r\n           else\r\n            img.src=imgPath2_1a+counter1+\".png\";\r\n          counter1++;\r\n          if(counter1>endImage1a) {\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1a\").onclick=play1a;\r\n            //img.src=imgPath1_1a+startImage+\".png\";\r\n          }\r\n\r\n         },100);\r\n      }\r\n\r\nfunction stop1a(){\r\n            clearInterval(interval1);\r\n            counter1=0;\r\n                        document.getElementById(\"play1a\").onclick=play1a;\r\n            img.src=imgPath1_1a+endImage2a+\".png\";\r\n}\r\n</script>\r\n</div>\r\n"

