'use strict';

export default function($stateProvider) {
  'ngInject';
  var angles = [];
  var path1 = '/assets/images/ch6_fig17A/ch6_fig17A00';
  var path2 = '/assets/images/ch6_fig17A/ch6_fig17A000';
  for(var i = 0; i < 41; i++){
    if (i <= 9){
      angles.push(path2 + i + '.png');
    }else{
      angles.push(path1 + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter06-angle-of-activation', {
      url: '/chapter06-angle-of-activation',
      template: '<chapter-06-angle-of-activation></chapter-06-angle-of-activation>',
      resolve: {
        angleFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(angles);
        }
      }
    });
}



