'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter06-summary.routes';

export class Chapter06SummaryComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter06-summary', [uiRouter])
  .config(routes)
  .component('chapter06Summary', {
    template: require('./chapter06-summary.html'),
    controller: Chapter06SummaryComponent,
    controllerAs: 'chapter06SummaryCtrl'
  })
  .name;



