'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter06-review-questions.routes';

export class Chapter06ReviewQuestionsComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter06-review-questions', [uiRouter])
  .config(routes)
  .component('chapter06ReviewQuestions', {
    template: require('./chapter06-review-questions.html'),
    controller: Chapter06ReviewQuestionsComponent,
    controllerAs: 'chapter06ReviewQuestionsCtrl'
  })
  .name;



