'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './equilibrium.routes';

export class EquilibriumComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.equilibrium', [uiRouter])
  .config(routes)
  .component('equilibrium', {
    template: require('./equilibrium.html'),
    controller: EquilibriumComponent,
    controllerAs: 'equilibriumCtrl'
  })
  .name;



