'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './action-and-reactions.routes';

export class ActionAndReactionsComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.action-and-reactions', [uiRouter])
  .config(routes)
  .component('actionAndReactions', {
    template: require('./action-and-reactions.html'),
    controller: ActionAndReactionsComponent,
    controllerAs: 'actionAndReactionsCtrl'
  })
  .name;



