import routes from './bends-in-flexible-wires.routes';

export class BendsInFlexibleWiresComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.bends-in-flexible-wires', [uiRouter])
  .config(routes)
  .component('bendsInFlexibleWires', {
    template: require('./bends-in-flexible-wires.html'),
    controller: BendsInFlexibleWiresComponent,
    controllerAs: 'bendsInFlexibleWiresCtrl'
  })
  .name;



