'use strict';

export default function($stateProvider) {
  'ngInject';
  var activation = [];
  var path1 = '/assets/images/ch6_fig26A/ch6_fig26A000';
  var path2 = '/assets/images/ch6_fig26A/ch6_fig26A00';
  for(var i = 0; i < 45; i++){
    if (i <= 9){
      activation.push(path1 + i + '.png');
    }else{
      activation.push(path2 + i + '.png');
    }
  }
  var imgPath1_17="/assets/images/ch6_fig26B/ch6_fig26B000";
  var imgPath2_17="/assets/images/ch6_fig26B/ch6_fig26B00";
  for(var i = 0; i < 46; i++){
    if (i <= 9){
      activation.push(imgPath1_17 + i + '.png');
    }else{
      activation.push(imgPath2_17 + i + '.png');
    }
  }
  console.log('here i am ');
  console.log(activation);
  $stateProvider
    .state('home.chapter06-activation', {
      url: '/chapter06-activation',
      template: '<chapter-06-activation></chapter-06-activation>',
      resolve: {
        activationFiles: function($ocLazyLoad, $q, preloader){
          console.log('activation');
          console.log(activation);
          return preloader.preloadImages(activation);
        }
      }
    });
}



