'use strict';
const angular = require('angular');

/*@ngInject*/
export function notesControllerController($uibModalInstance, $http, $timeout, $scope) {
  'ngInject';
  this.ok = ok.bind(this);
  this.cancel = function(){
    $uibModalInstance.dismiss();
  }
  this.notesObject = {};
  this.getAllNotes = getAllNotes.bind(this);
  this.getAllNotes();
  function ok(form, formObject){
    if (form.$valid){
      $http.post('/api/notes', formObject)
        .then((res) =>{
          this.message = 'Note added successfully';
          form.$setPristine(true)
          this.notesObject = {};
          $timeout(function () {
            this.message = null;
          }.bind(this), 3000);
        })
    }
  }
  function getAllNotes(){
    $http.get('/api/notes')
      .then((res) =>{
        this.allNotes = res.data;
      })
  }
}

export default angular.module('scApp.notesController', [])
  .controller('NotesControllerController', notesControllerController)
  .name;



