'use strict';

export default class LoginController {

  /*@ngInject*/
  constructor(Auth, $state, $scope, $location, $rootScope, $uibModalInstance, $http, $auth, $uibModal, $timeout) {
    'ngInject';
    this.Auth = Auth;
    this.HTTP = $http;
    this.auth = $auth;
    this.timeout = $timeout;
    this.uibModal = $uibModal;
    this.submitted1 = false;
    this.uibModalInstance = $uibModalInstance;
    this.isLoggedIn = Auth.isLoggedInSync;
    // this.uibModal = $uibModal;
    // this.uibModalInstance = $uibModalInstance;
    this.$state = $state;
    this.errors = {};
    this.errors1 = {};

  }

  login(form) {
    this.submitted = true;

    if(form.$valid) {
      this.Auth.login({
        email: this.user.email,
        password: this.user.password
      })
        .then(() => {
          this.uibModalInstance.dismiss();
          this.$state.go('home.title')
        })
        .catch(err => {
          console.log(err);
          this.errors.login = err.message;
        });
    }
  }
  register(form, formObject) {
    this.submitted1 = true;
    if(form.$valid) {
      return this.Auth.createUser({
        username: formObject.username,
        email: formObject.email,
        password: formObject.password
      })
        .then((result) => {
          this.uibModalInstance.dismiss();
          this.$state.go('home.title');
        })
        .catch(err => {
          err = err.data;
          this.errors = {};
          // Update validity of form fields that match the mongoose errors
          angular.forEach(err.errors, (error, field) => {
            form[field].$setValidity('mongoose', false);
            this.errors1[field] = error.message;
          });
        });
    }
  }
  socialLogin(provider){
    this.auth.authenticate(provider)
      .then(res =>{
        if (res.data.token){
          this.Auth.socialAuth(res.data.token);
          this.uibModalInstance.dismiss();
          this.$state.go('home.title');
        }
      })
  }
  submit(form, formData){
    if (form.$valid){
      this.submitted = true;
      this.HTTP.post('/api/users/forget',formData)
        .then(
          (result) =>{
            this.submitted = false;
            this.message = result.data.message;
            this.timeout(() =>{
              this.message = null;
            }, 3000)
          }
        )
        .catch((err) => {
          this.submitted = false;
          this.message = err.data.message;
          this.timeout(() =>{
            this.message = null;
          },3000)
        })
    }
  }

  openLogin(){
    this.uibModalInstance.dismiss();
    var modalInstance = this.uibModal.open({
      templateUrl: 'loginModal.html',
      windowTopClass: 'modal-transparent',
      controller: 'LoginController',
      controllerAs: 'vm',
      size: 'md'
    });
  }

  openReset(){
    this.uibModalInstance.dismiss();
    var modalInstance = this.uibModal.open({
      templateUrl: 'resetModal.html',
      controller: 'LoginController',
      windowTopClass: 'modal-transparent',
      controllerAs: 'vm',
      size: 'md'
    });
  }
  closeModal(){
    // this.uibModalInstance.dismiss();
  }
  goToRegister(){
    this.uibModalInstance.dismiss();
    var modalInstance = this.uibModal.open({
      templateUrl: 'signupModal.html',
      windowTopClass: 'modal-transparent',
      controller: 'LoginController',
      controllerAs: 'vm',
      size: 'md'
    });
  }
}



