'use strict';

export default function($stateProvider) {
  'ngInject';
  var chapter06ReviewImages = [];
  var imgPath1_25="/assets/images/ch6_Q1/ch6_Q2000";
  var imgPath2_25="/assets/images/ch6_Q1/ch6_Q200";
  for(var i = 0; i < 65; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_25 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_25 + i + '.png');
    }
  }
  var imgPath1_26="/assets/images/ch6_Q2A/ch6_Q4A000";
  var imgPath2_26="/assets/images/ch6_Q2A/ch6_Q4A00";
  for(var i = 0; i < 44; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_26 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_26 + i + '.png');
    }
  }
  var imgPath1_27="/assets/images/ch6_Q2B/ch6_Q4B000";
  var imgPath2_27="/assets/images/ch6_Q2B/ch6_Q4B00";
  for(var i = 0; i < 44; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_27 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_27 + i + '.png');
    }
  }
  var imgPath1_28="/assets/images/ch6_Q3/ch6_Q3000";
  var imgPath2_28="/assets/images/ch6_Q3/ch6_Q300";
  for(var i = 5; i < 67; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_28 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_28 + i + '.png');
    }
  }
  var imgPath1_29="/assets/images/ch6_Q4A/ch6_Q4A000";
  var imgPath2_29="/assets/images/ch6_Q4A/ch6_Q4A00";
  for(var i = 0; i < 24; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_29 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_29 + i + '.png');
    }
  }
  var imgPath1_30="/assets/images/ch6_Q4B/ch6_Q4B000";
  var imgPath2_30="/assets/images/ch6_Q4B/ch6_Q4B00";
  for(var i = 0; i < 24; i++){
    if (i <= 9){
      chapter06ReviewImages.push(imgPath1_30 + i + '.png');
    }else{
      chapter06ReviewImages.push(imgPath2_30 + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter06-review-questions', {
      url: '/chapter06-review-questions',
      template: '<chapter-06-review-questions></chapter-06-review-questions>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(chapter06ReviewImages);
        }
      }
    });
}



