'use strict';

export default function($stateProvider) {
  'ngInject';
  var hiddenImages = [];
  var imgPath1_6="/assets/images/ch5_fig16/ch5_fig16000";
  var imgPath2_6="/assets/images/ch5_fig16/ch5_fig1600";
  for(var i = 0; i < 34; i++){
    if (i <= 9){
      hiddenImages.push(imgPath1_6 + i + '.png');
    }else{
      hiddenImages.push(imgPath2_6 + i + '.png');
    }
  }
  var imgPath1_7="/assets/images/ch5_fig17/ch5_fig17000";
  var imgPath2_7="/assets/images/ch5_fig17/ch5_fig1700";
  for(var i = 0; i < 74; i++){
    if (i <= 9){
      hiddenImages.push(imgPath1_7 + i + '.png');
    }else{
      hiddenImages.push(imgPath2_7 + i + '.png');
    }
  }
  var imgPath1_8="/assets/images/ch5_fig20/ch5_fig20000";
  var imgPath2_8="/assets/images/ch5_fig20/ch5_fig2000";
  for(var i = 0; i < 20; i++){
    if (i <= 9){
      hiddenImages.push(imgPath1_8 + i + '.png');
    }else{
      hiddenImages.push(imgPath2_8 + i + '.png');
    }
  }
  var imgPath1_9="/assets/images/ch5_fig21/ch5_fig21000";
  var imgPath2_9="/assets/images/ch5_fig21/ch5_fig2100";
  for(var i = 0; i < 32; i++){
    if (i <= 9){
      hiddenImages.push(imgPath1_9 + i + '.png');
    }else{
      hiddenImages.push(imgPath2_9 + i + '.png');
    }
  }
  var imgPath1_10="/assets/images/ch5_fig22/ch5_fig22000";
  var imgPath2_10="/assets/images/ch5_fig22/ch5_fig2200";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      hiddenImages.push(imgPath1_10 + i + '.png');
    }else{
      hiddenImages.push(imgPath2_10 + i + '.png');
    }
  }
  $stateProvider
    .state('home.hidden-force-and-moments', {
      url: '/hidden-force-and-moments',
      template: '<hidden-force-and-moments></hidden-force-and-moments>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(hiddenImages);
        }
      }
    });
}



