'use strict';

export default function($stateProvider) {
  'ngInject';
  var equimages = [];
  var imgPath1_2="/assets/images/ch5_fig12/ch5_fig12000";
  var imgPath2_2="/assets/images/ch5_fig12/ch5_fig1200";
  for(var i = 0; i < 80; i++){
    if (i <= 9){
      equimages.push(imgPath1_2 + i + '.png');
    }else{
      equimages.push(imgPath2_2 + i + '.png');
    }
  }
  var imgPath1_3="/assets/images/ch5_fig13/ch5_fig13000";
  var imgPath2_3="/assets/images/ch5_fig13/ch5_fig1300";
  var imgPath3_3="/assets/images/ch5_fig13/ch5_fig130";
  for(var i = 87; i < 127; i++){
    if (i <= 9){
      equimages.push(imgPath1_3 + i + '.png');
    }
    else if (i < 100){
      equimages.push(imgPath2_3 + i + '.png');
    }else{
      equimages.push(imgPath3_3 + i + '.png');

    }
  }
  $stateProvider
    .state('home.equilibrium', {
      url: '/equilibrium',
      template: '<equilibrium></equilibrium>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(equimages);
        }
      }
    });
}



