'use strict';

export default function($stateProvider) {
  'ngInject';
  var bendImages = [];
  var imgPath1_1="/assets/images/ch8_fig5/ch8_fig5000";
  var imgPath2_1="/assets/images/ch8_fig5/ch8_fig500";
  for(var i = 0; i < 59; i++){
    if (i <= 9){
      bendImages.push(imgPath1_1 + i + '.png');
    }else{
      bendImages.push(imgPath2_1 + i + '.png');
    }
  }
  $stateProvider
    .state('home.bends-in-flexible-wires', {
      url: '/bends-in-flexible-wires',
      template: '<bends-in-flexible-wires></bends-in-flexible-wires>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(bendImages);
        }
      }
    });
}



