'use strict';

export default function($stateProvider) {
  'ngInject';
  var stepBendForcesImages = [];
  var imgPath1_1="/assets/images/ch8_fig6A/ch8_fig6A000";
  var imgPath2_1="/assets/images/ch8_fig6A/ch8_fig6A00";
  for(var i = 0; i < 44; i++){
    if (i <= 9){
      stepBendForcesImages.push(imgPath1_1 + i + '.png');
    }else{
      stepBendForcesImages.push(imgPath2_1 + i + '.png');
    }
  }
  var imgPath1_2="/assets/images/ch8_fig6B/ch8_fig6B000";
  var imgPath2_2="/assets/images/ch8_fig6B/ch8_fig6B00";
  for(var i = 0; i < 44; i++){
    if (i <= 9){
      stepBendForcesImages.push(imgPath1_2 + i + '.png');
    }else{
      stepBendForcesImages.push(imgPath2_2 + i + '.png');
    }
  }
  var imgPath1_3="/assets/images/ch8_fig6C/ch8_fig6C000";
  var imgPath2_3="/assets/images/ch8_fig6C/ch8_fig6C00";
  for(var i = 0; i < 34; i++){
    if (i <= 9){
      stepBendForcesImages.push(imgPath1_3 + i + '.png');
    }else{
      stepBendForcesImages.push(imgPath2_3 + i + '.png');
    }
  }
  var imgPath1_4="/assets/images/ch8_fig7/ch8_fig7000";
  var imgPath2_4="/assets/images/ch8_fig7/ch8_fig700";
  for(var i = 0; i < 52; i++){
    if (i <= 9){
      stepBendForcesImages.push(imgPath1_4 + i + '.png');
    }else{
      stepBendForcesImages.push(imgPath2_4 + i + '.png');
    }
  }
  $stateProvider
    .state('home.step-bend-forces-moments', {
      url: '/step-bend-forces-moments',
      template: '<step-bend-forces-moments></step-bend-forces-moments>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(stepBendForcesImages);
        }
      }
    });
}



