'use strict';

export default function($stateProvider) {
  'ngInject';
  var asyImages = [];
  var imgPath1_1="/assets/images/ch7_fig15/ch7_fig15A000";
  var imgPath2_1="/assets/images/ch7_fig15/ch7_fig15A00";
  for(var i = 0; i < 34; i++){
    if (i <= 9){
      asyImages.push(imgPath1_1 + i + '.png');
    }else{
      asyImages.push(imgPath2_1 + i + '.png');
    }
  }
  var imgPath1_2="/assets/images/ch7_fig16/ch7_fig16000";
  var imgPath2_2="/assets/images/ch7_fig16/ch7_fig1600";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      asyImages.push(imgPath1_2 + i + '.png');
    }else{
      asyImages.push(imgPath2_2 + i + '.png');
    }
  }
  var imgPath1_3="/assets/images/ch7_fig19/ch7_fig19000";
  var imgPath2_3="/assets/images/ch7_fig19/ch7_fig1900";
  for(var i = 0; i < 39; i++){
    if (i <= 9){
      asyImages.push(imgPath1_3 + i + '.png');
    }else{
      asyImages.push(imgPath2_3 + i + '.png');
    }
  }
  var imgPath1_4="/assets/images/ch7_fig20/ch7_fig20000";
  var imgPath2_4="/assets/images/ch7_fig20/ch7_fig2000";
  for(var i = 0; i < 37; i++){
    if (i <= 9){
      asyImages.push(imgPath1_4 + i + '.png');
    }else{
      asyImages.push(imgPath2_4 + i + '.png');
    }
  }
  var imgPath1_5="/assets/images/ch7_fig22/ch7_fig22000";
  var imgPath2_5="/assets/images/ch7_fig22/ch7_fig2200";
  for(var i = 0; i < 37; i++){
    if (i <= 9){
      asyImages.push(imgPath1_5 + i + '.png');
    }else{
      asyImages.push(imgPath2_5 + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter-07-asymmetrical-v-bend', {
      url: '/chapter-07-asymmetrical-v-bend',
      template: '<chapter-07-asymmetrical-v-bend></chapter-07-asymmetrical-v-bend>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(asyImages);
        }
      }
    });
}



