'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './second-law.routes';

export class SecondLawComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.second-law', [uiRouter])
  .config(routes)
  .component('secondLaw', {
    template: require('./second-law.html'),
    controller: SecondLawComponent,
    controllerAs: 'secondLawCtrl'
  })
  .name;



