'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './home.routes';

export class HomeComponent {
  /*@ngInject*/
  constructor($rootScope, $http, $state, $uibModal, $sce) {
    'ngInject';
    this.rootScope = $rootScope;
    $rootScope.$ctrl = {};
    this.url = this.rootScope.path.url;
    this.dynamicPopover = {
      content: 'Options',
      templateUrl: 'myPopoverTemplate.html',
      title: 'Title'
    };

    $rootScope.$on('routeChange', function(event, path){
      this.url = path.url;
      this.rootScope.$ctrl.url = path.url;
    }.bind(this));
    this.covers = [
        {
          name: 'Force and Moment',
          image: '/assets/images/cover1.jpg'
        },
        {
          name: 'Couple',
          image: '/assets/images/cover2.jpg'
        },
        {
          name: 'Different Types of Tooth Movement',
          image: '/assets/images/cover3.jpg'
        },
        {
          name: 'Couple to Force Ratio',
          image: '/assets/images/cover4.jpg'
        },
        {
          name: 'Equilibrium',
          image: '/assets/images/cover5.jpg'
        },
        {
          name: 'One-Couple System',
          image: '/assets/images/cover6.jpg',
          state: 'home.chapter06-one-couple-system-intro'
        },
        {
          name: 'Two-Couple System: Basic',
          image: '/assets/images/cover7.jpg',
          state: 'home.chapter-08-two-couple-basic-system-basic-intro'
        },
        {
          name: 'Two-Couple System: Advanced',
          image: '/assets/images/cover8.jpg',
          state: 'home.two-couple-system-advanced'
        }
      ]
      this.getBookMarks = getBookMarks.bind(this);
      this.getStateName = getStateName.bind(this);

      this.getBookMarks();

      this.openNotes = openNotes.bind(this);

      function openNotes(){
        var modalInstance = $uibModal.open({
          templateUrl: 'notesTemplate.html',
          controller: 'NotesControllerController',
          controllerAs: 'vm'
        });
      }

      this.lastViewed = function(){
        $http.get('/api/lastPages')
          .then(function(data){
            console.log(data);
            $state.go(data.data.name);
          })
      }

      function getBookMarks(){
        $http.get('/api/bookmarks')
          .then((response) =>{
            var bookmarks = response.data
            this.bookmarks = _.uniq(bookmarks, function(data){
              return data.name
            });
          })
      }

      function getStateName(state){
        return this.stateNames[state];
      }
      this.bookmark = function(){
        console.log('booksmar');
        $http.post('/api/bookmarks',{name: $state.current.name})
          .then((response) =>{
            this.bookmarks.push(response.data);
          })
      }
      this.openModal = openModal.bind(this);

      function openModal(){
        var modalInstance = $uibModal.open({
          templateUrl: 'bookmarkTemplate.html',
          controller: 'BookmarkModalController',
          windowTopClass: 'bookmark',
          controllerAs: 'vm',
          resolve: {
            bookmarkData: [function(){
              return this.bookmarks;
            }.bind(this)]
          }
        });
      }


  }
}

export default angular.module('scApp.home', [uiRouter])
  .config(routes)
  .component('home', {
    template: require('./home.html'),
    controller: HomeComponent
  })
  .name;



