'use strict';

export default function($stateProvider) {
  'ngInject';
  var images = [];
  var path1 = '/assets/images/ch6_fig7/ch6_fig7_new00';
  var path2 = '/assets/images/ch6_fig7/ch6_fig7_new000';
  for(var i = 0; i < 53; i++){
    if (i <= 9){
      images.push(path2 + i + '.png');
    }else{
      images.push(path1 + i + '.png');
    }
  }
  console.log(images);
  $stateProvider
    .state('home.chapter06-other-moments', {
      url: '/chapter06-other-moments',
      template: '<chapter-06-other-moments></chapter-06-other-moments>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          console.log(images);
          console.log('moments');
          return preloader.preloadImages(images);
        }
      }
    });
}



