'use strict';

export default function($stateProvider) {
  'ngInject';
  var reviewQuestionsImages = [];
  var imgPath1_11="/assets/images/ch5_q1/ch5_q1000";
  var imgPath2_11="/assets/images/ch5_q1/ch5_q100";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      reviewQuestionsImages.push(imgPath1_11 + i + '.png');
    }else{
      reviewQuestionsImages.push(imgPath2_11 + i + '.png');
    }
  }
  var imgPath1_12="/assets/images/ch5_q2/ch5_q2000";
  var imgPath2_12="/assets/images/ch5_q2/ch5_q200";
  for(var i = 0; i < 47; i++){
    if (i <= 9){
      reviewQuestionsImages.push(imgPath1_12 + i + '.png');
    }else{
      reviewQuestionsImages.push(imgPath2_12 + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter05-review-questions', {
      url: '/chapter05-review-questions',
      template: '<chapter-05-review-questions></chapter-05-review-questions>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(reviewQuestionsImages);
        }
      }
    });
}



