'use strict';

export default function($stateProvider) {
  'ngInject';
  var wireShaptesImages = [];
  var imgPath1_1a="/assets/images/ch4_fig16/ch4_fig16000";
  var imgPath2_1a="/assets/images/ch4_fig16/ch4_fig1600";
  for(var i = 0; i < 74; i++){
    if (i <= 9){
      wireShaptesImages.push(imgPath1_1a + i + '.png');
    }
    else{
      wireShaptesImages.push(imgPath2_1a + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter04-wire-shapes', {
      url: '/chapter04-wire-shapes',
      template: '<chapter-04-wire-shapes></chapter-04-wire-shapes>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(wireShaptesImages);
        }
      }
    });
}



