'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter04-intro.routes';

export class Chapter04IntroComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter04-intro', [uiRouter])
  .config(routes)
  .component('chapter04Intro', {
    template: require('./chapter04-intro.html'),
    controller: Chapter04IntroComponent,
    controllerAs: 'chapter04IntroCtrl'
  })
  .name;



