'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter05-summary.routes';

export class Chapter05SummaryComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter05-summary', [uiRouter])
  .config(routes)
  .component('chapter05Summary', {
    template: require('./chapter05-summary.html'),
    controller: Chapter05SummaryComponent,
    controllerAs: 'chapter05SummaryCtrl'
  })
  .name;



