'use strict';
const angular = require('angular');

export default angular.module('scApp.search', [])
  .directive('search', function() {
    return {
      templateUrl: 'app/search/search.html',
      restrict: 'EA',
      link: function(scope, element, attrs) {
        element.bind("keydown keypress", function (event) {
            if(event.which === 13) {
              if (window.find && window.getSelection) {
                  document.designMode = "on";
                  var sel = window.getSelection();
                  sel.collapse(document.body, 0);

                  while (window.find(event.target.value)) {
                      document.execCommand("HiliteColor", false, "silver");
                      sel.collapseToEnd();
                  }
                  document.designMode = "off";
              } else if (document.body.createTextRange) {
                  var textRange = document.body.createTextRange();
                  while (textRange.findText(event.target.value)) {
                      textRange.execCommand("BackColor", false, "silver");
                      textRange.collapse(false);
                  }
              }

              event.preventDefault();
            }
        });
      },
      controller:[function(){
      }],
      controllerAs: 'vm'
    };
  })
  .name;



