'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter05-certificate.routes';

export class Chapter05CertificateComponent {
  /*@ngInject*/
  constructor(Auth) {
    'ngInject';
    this.date = new Date();
    this.getCurrentUser = Auth.getCurrentUserSync;
  }
}

export default angular.module('scApp.chapter05-certificate', [uiRouter])
  .config(routes)
  .component('chapter05Certificate', {
    template: require('./chapter05-certificate.html'),
    controller: Chapter05CertificateComponent,
    controllerAs: 'vm'
  })
  .name;



