'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter05-recommended-reading.routes';

export class Chapter05RecommendedReadingComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter05-recommended-reading', [uiRouter])
  .config(routes)
  .component('chapter05RecommendedReading', {
    template: require('./chapter05-recommended-reading.html'),
    controller: Chapter05RecommendedReadingComponent,
    controllerAs: 'chapter05RecommendedReadingCtrl'
  })
  .name;



