'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './first-law.routes';

export class FirstLawComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.first-law', [uiRouter])
  .config(routes)
  .component('firstLaw', {
    template: require('./first-law.html'),
    controller: FirstLawComponent,
    controllerAs: 'firstLawCtrl'
  })
  .name;



