'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './chapter06-activation.routes';

export class Chapter06ActivationComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('scApp.chapter06-activation', [uiRouter])
  .config(routes)
  .component('chapter06Activation', {
    template: require('./chapter06-activation.html'),
    controller: Chapter06ActivationComponent,
    controllerAs: 'chapter06ActivationCtrl'
  })
  .name;



