'use strict';
const angular = require('angular');

/*@ngInject*/
export function lastPageService($http, $state) {
	var service = {};
  service.post = post;

  function post(){
		console.log('last page....');
    return $http.post('/api/lastPages', {name: $state.current.name});
  }
	return service;
}

export default angular.module('scApp.lastPage', [])
  .service('lastPage', lastPageService)
  .name;



