'use strict';

export default function($stateProvider) {
  'ngInject';
  var syImages = [];
  var imgPath1_1="/assets/images/ch7_fig5A/ch7_fig5A000";
  var imgPath2_1="/assets/images/ch7_fig5A/ch7_fig5A00";
  for(var i = 0; i < 74; i++){
    if (i <= 9){
      syImages.push(imgPath1_1 + i + '.png');
    }else{
      syImages.push(imgPath2_1 + i + '.png');
    }
  }
  var imgPath1_2="/assets/images/ch7_fig5B/ch7_fig5B000";
  var imgPath2_2="/assets/images/ch7_fig5B/ch7_fig5B00";
  for(var i = 0; i < 74; i++){
    if (i <= 9){
      syImages.push(imgPath1_2 + i + '.png');
    }else{
      syImages.push(imgPath2_2 + i + '.png');
    }
  }
  var imgPath1_3="/assets/images/ch7_fig6/ch7_fig6000";
  var imgPath2_3="/assets/images/ch7_fig6/ch7_fig600";
  for(var i = 0; i < 14; i++){
    if (i <= 9){
      syImages.push(imgPath1_3 + i + '.png');
    }else{
      syImages.push(imgPath2_3 + i + '.png');
    }
  }
  var imgPath1_4="/assets/images/ch7_fig7A/ch7_fig7A000";
  var imgPath2_4="/assets/images/ch7_fig7A/ch7_fig7A00";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      syImages.push(imgPath1_4 + i + '.png');
    }else{
      syImages.push(imgPath2_4 + i + '.png');
    }
  }
  var imgPath1_5="/assets/images/ch7_fig8/ch7_fig8000";
  var imgPath2_5="/assets/images/ch7_fig8/ch7_fig800";
  for(var i = 0; i < 29; i++){
    if (i <= 9){
      syImages.push(imgPath1_5 + i + '.png');
    }else{
      syImages.push(imgPath2_5 + i + '.png');
    }
  }
  $stateProvider
    .state('home.chapter-07-symmetrical-v-bend', {
      url: '/chapter-07-symmetrical-v-bend',
      template: '<chapter-07-symmetrical-v-bend></chapter-07-symmetrical-v-bend>',
      resolve: {
        imagesFiles: function($ocLazyLoad, $q, preloader){
          return preloader.preloadImages(syImages);
        }
      }
    });
}



