'use strict';

export function routeConfig($urlRouterProvider, $locationProvider, NotificationProvider, $ocLazyLoadProvider, $authProvider) {
  'ngInject';
  // NotificationProvider.setOptions({
  //     delay: 100,
  //     startTop: 20,
  //     startRight: 10,
  //     verticalSpacing: 20,
  //     horizontalSpacing: 20,
  //     positionX: 'left',
  //     positionY: 'bottom'
  // });

  $authProvider.facebook({
    clientId: '1780990305515917',
    url: '/api/facebook',
    redirectUri: window.location.origin + '/api/facebook'
  });

  $ocLazyLoadProvider.config({
    debug: true
  });

  $urlRouterProvider.otherwise('/');

  $locationProvider.html5Mode(true);
}



